body {
  margin: 0;
  font-size: 16px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

td {
  text-align: center;
}

.total_values{
  text-align: center;
  margin: 1.5rem 0;
}

.total_values h1 {
  font-size: 2.3rem;
}

.first_flex {
  display: flex;
  background-color: black;
  justify-content: space-between;
}


#nav_headline span{
  color: #fff;
  font-size: 2rem;
}

.first_flex > div:nth-child(1) {
  padding-top: 8px;
  padding-left: 30px;
}

.first_flex > div:nth-child(2) {
  padding: 20px 30px 20px 0;
}

.remitance_container {
  height: 100vh;
}

.remitance_image {
  width: 50%;
  margin: 0 auto;
}
.remitance_image img{
  display: block;
  max-width: 100%;
  max-height: 500px ;
  object-fit: cover;
}

.header_parent {
  width: 50%;
  margin: 20px auto 0 auto;
}

.header_parent h1 {
  font-size: 2.2rem;
}

.remitance_form {
  margin-top: 20px;
}

.homepage_container {
  height: 100vh;
}

.left_side,.right_side {
  margin: 0 auto;
}

/* css for moible devices */

@media screen and (max-width:567px) {
  /* body {
    font-size: 10px;
  } */

  .total_values{
    text-align: center;
    margin: 1rem 1rem;
  }
  
  .total_values h1 {
    font-size: 1.3rem;
  }

  .mm{
    display: flex;
  }

  .first_flex {
    max-width: 100%;
    overflow: hidden;
    justify-content: center;
  }
  
  .first_flex > div:nth-child(1) {
    padding-left: 0;
    padding-bottom: 14px;
  }

  #nav_headline span{
    font-size: 1.5rem;
  }
  

  .first_flex > div:nth-child(2) {
    display: none;
  }

  .remitance_image {
    display: none;
  }

  .remitance_container img {
    object-fit: cover;
  }

  .header_parent {
    margin: 0;
    width: 100%;
  }

  .header_parent h1{
    margin-top: 20px;
    margin-left : 20px;
    /* margin-bottom: 20px; */
    font-size: 30px;
  }

  .remitance_form {
    margin: 0 20px;
  }

  .homepage_container {
    max-width: 100%;
    overflow: hidden;
    justify-content: center;
    height: auto;
    margin: 30px 0;
  }

  .left_side,.right_side {
    margin-bottom: 30px;
  }

  .left_side h1 {
    font-size: 1.2rem;
    line-height: 1.5;
  }
  .right_side h1 {
    font-size: 1.5rem;
  }
}
